								/* 获取用户信息2 */
								// 获取用户基础信息
								uni.getUserInfo({
									provider: _self.provider,
									success: function(infoRes) {
										console.log('用户昵称为：' + JSON.stringify(infoRes.userInfo));
									}
								});
								// 获取用户的手机号



							},
						});


					}
				});


			},
